import React from "react";
import { page_config } from "../page_config";
import assert from "@brillout/assert";
import gearIcon from "./gear.svg";
import heartIcon from "./heart.svg";

export { donationPageConfig };

function donationPageConfig({
  projectName,
  projectIcon,
  projectGit,
  npmName,
  maintainers,
}) {
  assert(projectName && projectIcon && projectGit && npmName && maintainers);
  const route = "/d/" + npmName;
  return page_config(
    () => (
      <PackagePage {...{ maintainers, projectName, projectIcon, projectGit }} />
    ),
    projectName,
    { route, no_header: true }
  );
}

function PackagePage({ projectName, maintainers, projectIcon, projectGit }) {
  return (
    <>
      <Header {...{ projectName, projectIcon, maintainers, projectGit }} />
      <div style={{ clear: "both" }} />
      <Maintainers {...{ maintainers }} />
    </>
  );
}

function Header({ projectName, projectIcon, maintainers, projectGit }) {
  return (
    <>
      <div
        className="h1-margin-top"
        style={{ display: "flex", alignItems: "end" }}
      >
        <img
          className="project-icon"
          src={projectIcon}
          style={{
            marginRight: 20,
            width: 100,
          }}
        />
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
          }}
        >
          <h1 style={{ marginBottom: 8, marginTop: 0 }}>{projectName}</h1>
          <div>
            <img
              src={gearIcon}
              style={{
                height: 22,
                width: 22,
                verticalAlign: "middle",
                marginRight: 4,
              }}
            />
            <a href={projectGit}>{prettify_url(projectGit)}</a>
          </div>
          {maintainers.map(({ maintainerName }, key) => (
            <div
              {...{
                style: {
                  marginTop: 3,
                },
                key,
              }}
            >
              <img
                className="icon"
                src={heartIcon}
                style={{
                  height: 22,
                  width: 22,
                  verticalAlign: "middle",
                  position: "relative",
                  top: -1,
                  marginRight: 4,
                }}
              />
              {maintainerName}
            </div>
          ))}
        </div>
      </div>
    </>
  );
}

function Maintainers({ maintainers }) {
  return (
    <>
      {maintainers.map(
        ({ maintainerPhoto, maintainerText, maintainerName }, key) => {
          return (
            <div className="h2-margin-top" {...{ key }}>
              <img
                src={maintainerPhoto}
                style={{
                  float: "right",
                  marginLeft: 20,
                  marginBottom: 20,
                  marginTop: 12,

                  width: 228,
                  maxWidth: "100%",
                  height: 316,

                  border: "1px solid #b2b2b2",
                  borderRadius: 5,
                }}
              />
              <h2 className="maintainer-title" style={{ marginTop: 0 }}>
                {maintainerName}
              </h2>
              {React.createElement(maintainerText)}
            </div>
          );
        }
      )}
    </>
  );
}

function prettify_url(url) {
  return url.replace(/^(?:https?:\/\/)?(?:www\.)?/i, "");
}
